.dropdown-button {
  width: 208px;
  height: 40px;
  padding: 8px 16px;
  line-height: 0;
  border-radius: 2px;
  border: 2px solid #677171;
  color: #132323;

  &::after {
    content: '';
    display: block;
    width: 24px;
    height: 24px;
    background-image: url('../../../assets/icons/chevron-down-default.svg');
  }

  &:hover {
    background-color: #fff;
    border-color: #422318;
  }

  &:hover::after {
    background-image: url('../../../assets/icons/chevron-down-hover.svg');
  }
}

button {
  &.dropdown-button {
    justify-content: space-between;
  }
}

.dropdown-options {
  position: absolute;
  padding: 8px;
  width: 208px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  border-radius: 0 0 2px 2px;
  background-color: #fafafa;
  box-shadow: 3px 8px 10px 0px rgba(19, 35, 35, 0.07);
  z-index: 10;
}

.dropdown-option {
  padding: 8px 4px;
  color: #132323;

  & label {
    display: flex;
    align-items: center;
    gap: 8px;

    & input {
      width: 24px;
      height: 24px;
    }
  }
}

.dropdown-option:not(:last-child) {
  border-bottom: 0.5px solid #d9d9d9;
}
