.project-page__gallery-list {
  padding-block: 56px;
  row-gap: 24px;
}

.project__main-image {
  height: 596px;
  position: absolute;
  top: 0;
  right: 0;
}

.project__title {
  margin-bottom: 16px;
  text-align: center;
}

.project-attributes {
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  color: #919898;
}

.project__description {
  color: #132323;
}

