body {
  @include text-style($fs-main, 400, $lh-150);
  background-color: #fafafa;
}

.page {
  padding: 220px 0 80px;
}

.container {
  margin: 0 auto;
  padding-inline: 56px;
  max-width: 1440px;
}

.bg-image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
}

.primary-button {
  border-radius: 2px;
  color: #fff;
  background-color: #8e3a1b;
}
